.App {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #f8f9fa;
  border: 1px solid #ced4da;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}
body {
  font-family: 'Barlow', sans-serif;
}

h1 {
  color: #007bff;
}

input {
  width: 80%;
  padding: 10px;
  margin: 10px 0;
  font-size: 16px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  box-sizing: border-box;
}

button {
  background-color: #28a745;
  color: white;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}

#results-container {
  margin-top: 20px;
  animation: fadeIn 0.5s ease-out;
  border: 1px solid #ced4da;
  border-radius: 5px;
  box-sizing: border-box;
}

p {
  margin: 10px 0;
  font-size: 18px;
  color: #333;
}

.Toastify__toast-container {
  width: 300px;
}

.Toastify__toast--info {
  background-color: #007bff;
  color: #fff;
}

.Toastify__toast--info:hover {
  background-color: #0056b3;
}


.result-item {
  margin-bottom: 20px;
  padding: 10px;
  
  background-color: #f9f9f9;
}

.result-detail {
  margin: 10px 0;
  font-size: 22px;
  color: #333;
}
.info-heading {
  color: #007bff;
  display: flex;
  align-items: center;
  justify-content: center; /* Center horizontally */
  border: 2px solid #007bff;
  padding: 10px; /* Increased padding for better appearance */
  border-radius: 10px; /* Increased border-radius for rounded corners */
  margin: 20px 0; /* Add some vertical margin */
}

.info-heading::before {
  content: '\1F4D6'; /* Correct Unicode character for Material Icons info_outline */
  font-family: 'Material Icons Outlined';
  font-size: 28px;
  margin-right: 8px;
  color: #007bff; /* Color for the info icon */
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.typing-animation {
  overflow: hidden;
  white-space: nowrap;
  width: 0;
  animation: typingAnimation 2s steps(40, end) forwards;
}

@keyframes typingAnimation {
  to {
    width: 100%;
  }
}

